import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Context } from '../models/Enums';
import { Facet, Mappers } from '../models/Search';
import { DebugFlags } from '../models/SearchActions';
import { AppState } from '../models/SearchState';
import { triggerSearch } from '../redux/actions';
import {
  getResults,
  getIsLoading,
  getFacet,
  getPaging,
  getSearchTerm,
  getFilters,
} from '../redux/selectors';

type UseMiniListProps = {
  id: string;
  config?: Facet;
  debug?: DebugFlags;
  defaultLang?: string;
  excludeIds?: string[];
  mapper?: Mappers['results'];
  mappers?: Mappers;
  params?: { [key: string]: string };
};

const useMinilist = (
  {
    id,
    config,
    excludeIds,
    mapper,
    mappers,
    params,
    defaultLang,
    debug,
  }: UseMiniListProps = { id: '' }
) => {
  const dispatch = useDispatch();

  const { facet, filters, isLoading, pagingInfo, results, searchTerm } =
    useSelector((state: AppState) => ({
      facet: getFacet(state, id, Context.minilist).toJS(),
      filters: getFilters(state, id, Context.minilist).toJS(),
      isLoading: getIsLoading(state, Context.minilist, id),
      pagingInfo: getPaging(state, id, Context.minilist).toJS(),
      results: getResults(state, id, Context.minilist).toJS(),
      searchTerm: getSearchTerm(state),
    }));

  useEffect(() => {
    if (id && (mapper || (mappers && mappers.results))) {
      dispatch(
        triggerSearch({
          config,
          context: Context.minilist,
          defaultLang,
          facet: id,
          mapper,
          mappers,
          params,
          excludeIds,
          debug,
        })
      );
    }
  }, [dispatch, excludeIds, id, defaultLang, params]);

  return {
    filters,
    isLoading,
    pagingInfo,
    results,
    searchTerm,
    title: facet.title,
  };
};

export default useMinilist;
