import { List, Map, OrderedMap, Set } from 'immutable';
import { Entry } from 'contensis-delivery-api/lib/models';

import { Context } from '../models/Enums';
import { Listing } from '../models/Search';
import { AppState, Tab } from '../models/SearchState';
import {
  filtering,
  filterItem,
  initialState,
  searchFacet,
  searchTab,
} from './schema';

export const getSearchContext = (state: AppState): Context =>
  state.getIn(['search', 'context'], Context.facets);

export const getCurrent = (
  state: Map<'search', typeof initialState>,
  context = Context.facets
) =>
  context === Context.facets
    ? getCurrentFacet(state)
    : getCurrentListing(state);

export const getCurrentFacet = (state: AppState): string =>
  state.getIn(['search', 'currentFacet']);

export const getCurrentListing = (state: AppState): string =>
  state.getIn(['search', 'currentListing']);

export const getCurrentTab = (state: AppState): Map<string, Tab[keyof Tab]> =>
  state.getIn(['search', Context.facets, getCurrentFacet(state), 'tabId'], 0);

export const getFacets = (
  state: AppState
): OrderedMap<string, typeof searchFacet> =>
  state.getIn(['search', Context.facets], OrderedMap());

export const getTabFacets = (state: AppState) =>
  getFacets(state).filter(
    (v, key) =>
      getFacets(state).getIn([key, 'tabId'], 0) === getCurrentTab(state)
  ) as OrderedMap<string, typeof searchFacet>;

export const getFacetTitles = (state: AppState) =>
  getFacets(state)
    .map((facet = Map(), key) => ({
      key,
      title: facet.get('title') as string | undefined,
      totalCount: facet.getIn(['pagingInfo', 'totalCount']) as number,
    }))
    .toIndexedSeq()
    .toArray();

export const getFacet = (
  state: AppState,
  facetName = '',
  context = Context.facets
) => {
  const currentFacet = facetName || getCurrentFacet(state);
  return state.getIn(['search', context, currentFacet], Map());
};

export const getListing = (
  state: AppState,
  listing = ''
): Map<string, Listing[keyof Listing]> => {
  const currentListing = listing || getCurrentListing(state);
  return state.getIn(['search', Context.listings, currentListing], Map());
};

export const getFilters = (
  state: AppState,
  facet: string,
  context = Context.facets
): Map<string, typeof filtering> => {
  return state.getIn(
    ['search', context, facet || getCurrent(state, context), 'filters'],
    Map()
  );
};

export const getRenderableFilters = (
  state: AppState,
  facet = '',
  context = Context.facets
) =>
  getFilters(state, facet, context).filter(
    (f = Map()) => f.get('renderable', true) as boolean
  );

export const getFiltersToLoad = (
  state: AppState,
  facet: string,
  context = Context.facets
) => {
  const filters = getFilters(state, facet, context);
  const loadedFilters = filters.map(
    (f = Map()) =>
      ((f.get('items') || List()) as List<typeof filterItem>).filter(i => {
        const title = i?.get('title');
        return typeof title !== 'undefined' && !!title;
      }).size > 0 && f.get('isError', false) === false
  );
  return loadedFilters
    .map((isLoaded, filterKey) => (!isLoaded ? filterKey : null))
    .toList()
    .filter(f => !!f);
};

// We lowercase the filter key unless it's an ISO date string where the T must be uppercase
export const getSelectedFilters = (
  state: AppState,
  facet = '',
  context = Context.facets
) => {
  const filters = getFilters(state, facet, context);
  const isoDateRegex = RegExp(/\d{4}-[01]\d-[0-3]\dT[0-2]\d:[0-5]\d:[0-5]\d/);

  const selectedFilters = filters.map((filter = Map()) =>
    (
      (filter.get('items') as List<typeof filterItem>) ||
      List<typeof filterItem>()
    )
      .filter(item => !!item?.get('isSelected', false))
      .map(item => {
        const key = item?.get('key', '') as string;
        const isIsoDate = isoDateRegex.test(key);
        return isIsoDate ? key : key.toLowerCase();
      })
  );
  return selectedFilters as Map<string, List<string>>;
};

export const getResults = (
  state: AppState,
  current = '',
  context = Context.facets
) => {
  return state.getIn(
    ['search', context, current || getCurrent(state, context), 'results'],
    List()
  ) as List<any>;
};

export const getIsInternalPaging = (
  state: AppState,
  current: string,
  context = Context.facets
) => {
  return state.getIn(
    [
      'search',
      context,
      current || getCurrent(state, context),
      'queryParams',
      'internalPaging',
    ],
    false
  );
};

export const getIsLoaded = (
  state: AppState,
  context = Context.facets,
  facet?: string
) => {
  return !!state.getIn(
    ['search', context, facet || getCurrent(state, context), 'queryDuration'],
    0
  );
};

