export const getFacetAuthentication = (state: AppState, facet: string) =>
  state.getIn(['search', Context.facets, facet, 'authentication']);

export const getFeaturedEntryIds = (state: AppState) => {
  const currentFacet = getCurrentFacet(state);
  const entryIds = (
    state.getIn([
      'search',
      Context.facets,
      currentFacet,
      'featuredEntries',
      'items',
    ]) as Map<string, Entry[keyof Entry]>
  ).map(entry => entry.getIn(['sys', 'id']) as string);
  return entryIds;
};

export const getSearchTerm = (state: AppState) =>
  state.getIn(['search', 'term']);

export const getSearchTabs = (state: AppState): List<typeof searchTab> =>
  state.getIn(['search', 'tabs']);

export const getQueryParams = (
  state: AppState,
  current = '',
  context = Context.facets
) => {
  return state.getIn(
    ['search', context, current || getCurrent(state, context), 'queryParams'],
    Map()
  ) as Map<string, any>;
};

export const getQueryParameter = (
  {
    state,
    facet,
    context = Context.facets,
  }: { state: AppState; facet?: string; context?: Context },
  key: string,
  ifnull: any = null
) => {
  return getQueryParams(state, facet, context).get(key, ifnull) || ifnull;
};

