// eslint-disable no-console
import { List, Map, OrderedMap, fromJS } from 'immutable';

import {
  entries,
  filterItem,
  searchFacet,
  initialState,
  filtering,
} from './schema';
import {
  APPLY_CONFIG,
  CLEAR_FILTERS,
  EXECUTE_SEARCH,
  EXECUTE_SEARCH_ERROR,
  LOAD_FILTERS,
  LOAD_FILTERS_COMPLETE,
  LOAD_FILTERS_ERROR,
  SET_ROUTE_FILTERS,
  SET_SEARCH_FILTERS,
  SET_SEARCH_ENTRIES,
  UPDATE_SEARCH_TERM,
  UPDATE_SELECTED_FILTERS,
  UPDATE_SORT_ORDER,
  UPDATE_PAGE_INDEX,
} from './types';
import { toArray } from '../search/util';
import { ConfigTypes, Facet, Listing, SearchConfig } from '../models/Search';
import { Context } from '../models/Enums';

const addConfigToState = (state: typeof initialState, action: any) => {
  const { context, facet, config } = action;
  // Adding or changing the config of a single facet, listing or minilist
  if (context && facet && config) {
    return state.setIn([context, facet], fromJS(config));
  } else if (config) {
    // Changing the entire search config
    const newState = fromJS(config);
    return newState;
  }
  return state;
};

const generateSearchFacets = (context: Context, config: SearchConfig) => {
  let facets = OrderedMap<string, OrderedMap<string, typeof searchFacet>>();
  if (config) {
    const thisConfig: ConfigTypes = config[context] || {};
    if (Object.keys(thisConfig).length > 0) {
      Object.entries(thisConfig).map(
        ([facetName, facet]: [string, Facet | Listing]) => {
          const newFacet = searchFacet.merge(fromJS(facet));
          if (!('isDisabled' in facet) || facet.isDisabled !== true)
            facets = facets.set(
              facetName,
              newFacet as OrderedMap<string, typeof searchFacet>
            );
        }
      );
    }
  }
  return facets;
};

const generateFiltersState = (
  {
    facet,
    params,
    context,
    isCurrentFacet,
  }: {
    facet: string;
    params: { [k: string]: string };
    context: Context;
    isCurrentFacet: boolean;
  },
  state: typeof initialState
) => {
  // Remove filters we know about from params
  const filterParams = fromJS({
    ...params,
    facet: undefined,
    orderBy: undefined,
    pageIndex: undefined,
    term: undefined,
  }) as Map<string, string>;

  // Get any existing filters and normalise the items[]
  // so we can start off with isSelected is false
  let filters = state
    .getIn([context, facet, 'filters'], Map({}))
    .map((filter: typeof filtering) =>
      isCurrentFacet || filter.get('isGrouped')
        ? filter.set(
            'items',
            (filter.get('items') as List<typeof filterItem>).map(item =>
              item?.set('isSelected', false)
            ) as List<typeof filterItem>
          )
        : filter
    );

  const addFilterItem = (
    filters: Map<string, typeof filtering>,
    paramKey: string,
    paramValue: string
  ) =>
    // Iterate through all filters within the facet,
    // if the paramKey matches the filter key
    // get the existing items list, and see if that filter
    // already exists, if so set isSelected to true,
    // if not create a new filterItem, setting the key only
    // so we can match this key later on when we load the filters
    filters.map((filter = Map(), key) => {
      if (paramKey !== key || (!isCurrentFacet && !filter.get('isGrouped'))) {
        return filter;
      } else {
        const items = (filter.get('items') || List()) as List<
          typeof filterItem
        >;
        const itemIndex = items.findIndex(
          item => (item?.get('key') as unknown as string) === paramValue
        );
        if (items.size > 0 && itemIndex !== -1)
          return filter.setIn(['items', itemIndex, 'isSelected'], true);

        return filter.set(
          'items',
          ((filter.get('items') || List()) as List<typeof filterItem>).push(
            filterItem.set('key', paramValue as any).set('isSelected', true)
          )
        );
      }
    });

  // For each value found in filterParams
  // we are looking to split that value into multiple by any comma
  // and then either set isSelected for an existing filterItem
  // or push an item to the filters.{ key }.items[] array
  // giving it only the key (entry guid) that can be taken to filter
  // the search results during SSR without needing to fetch the filters first
  filterParams.map(
    (paramValue, paramName = '') =>
      paramValue &&
      paramValue
        .split(',')
        .map(pVal => (filters = addFilterItem(filters, paramName, pVal)))
  );

  return filters;
};

const resetFacets = (state: typeof initialState, context: Context) =>
  OrderedMap(
    (state.get(context) as Map<string, typeof searchFacet>).map(
      (v = OrderedMap()) => resetFacet(v)
    )
  );

const resetFacet = (facet: typeof searchFacet) =>
  facet
    .setIn(['pagingInfo', 'pagesLoaded'], fromJS([]))
    .setIn(['pagingInfo', 'pageIndex'], 0)
    .setIn(['queryDuration'], 0);

export default (config: SearchConfig) => {
  // Add facets from SearchConfig to initialState
  const initState = initialState
    .set('tabs', fromJS(config.tabs))
    .set('facets', generateSearchFacets(Context.facets, config))
    .set('listings', generateSearchFacets(Context.listings, config))
    .set('minilist', generateSearchFacets(Context.minilist, config));

  return (state: typeof initState = initState, action: any) => {
    const context = state.get('context') as Context;
    const current = state.get(
      context !== 'listings' ? 'currentFacet' : 'currentListing'
    ) as string;

    switch (action.type) {
      case APPLY_CONFIG: {
        return addConfigToState(state, action);
      }
      case CLEAR_FILTERS: {
        const currentFilters = state.getIn([
          context,
          current,
          'filters',
        ]) as Map<string, typeof filtering>;
        return state
          .setIn(
            [context, current, 'filters'],
            currentFilters.map(filter => {
              const filterItems = ((filter && filter.get('items')) ||
                []) as List<typeof filterItem>;
              return filter?.set(
                'items',
                filterItems.map(item => item?.set('isSelected', false)) as List<
                  typeof filterItem
                >
              );
            })
          )
          .setIn([context, current, 'queryDuration'], 0)
          .setIn([context, current, 'pagingInfo', 'pagesLoaded'], fromJS([]));
      }
      case EXECUTE_SEARCH: {
        return state.setIn(
          [action.context, action.facet, 'entries', 'isLoading'],
          true
        );
      }
      case EXECUTE_SEARCH_ERROR: {
        return state.setIn(
          [action.context, action.facet, 'entries'],
          entries.set('isError', true).set('error', fromJS(action.error))
        );
      }
      case LOAD_FILTERS: {
        const { facetKey, filtersToLoad } = action;
        const filters = state.getIn([
          action.context,
          facetKey,
          'filters',
        ]) as Map<string, typeof filtering>;

        return state.setIn(
          [action.context, facetKey, 'filters'],
          filters.map((filter = Map(), filterKey) =>
            filtersToLoad.find((f: string) => f === filterKey)
              ? filter.set('isLoading', true)
              : filter
          )
        );
      }
      case LOAD_FILTERS_ERROR:
      case LOAD_FILTERS_COMPLETE: {
        const { facetKey, filterKey, nextFilter } = action;
        const filter = state.getIn([
          action.context,
          facetKey,
          'filters',
          filterKey,
        ]);

        if (
          !(nextFilter.items && nextFilter.items.length > 0) &&
          filter.get('items').size >= nextFilter.items.length
        )
          // Preserve items already in state
          return state.setIn(
            [action.context, facetKey, 'filters', filterKey],
            filter.set('isLoading', false).set('isError', nextFilter.isError)
          );

        return state.setIn(
          [action.context, facetKey, 'filters', filterKey],
          filter.merge(fromJS(nextFilter))
        );
      }
      case SET_ROUTE_FILTERS: {
        const { facet, params, context } = action;
        const { term = '', pageIndex, orderBy } = params;

        const stateTerm = state.get('term');
        const tabId = state.getIn([context, facet, 'tabId'], 0);

        // Reset the facet if the search term has changed, or if the any of
        // the filters have changed
        const resetAllFacets = stateTerm && term !== stateTerm;
        let resetCurrentFacet = false;

        // Add filter values in params to the matched filters in state for the current facet
        // causing unfetched filter items to be generated with isSelected: true
        // or existing filter items to be tagged with isSelected: true
        const nextFacets = (
          state.get(context) as OrderedMap<string, typeof searchFacet>
        ).map((stateFacet = Map(), facetName = '') => {
          const isCurrentFacet = facetName === facet;
          const nextFilters = generateFiltersState(
            {
              facet: facetName,
              params,
              context,
              isCurrentFacet,
            },
            state
          );

          resetCurrentFacet =
            state.getIn(['config', 'isLoaded'], false) === true &&
            !nextFilters.equals(stateFacet.get('filters'));

          return (resetCurrentFacet ? resetFacet(stateFacet) : stateFacet)
            .set('filters', nextFilters)
            .setIn(['queryParams', 'dynamicOrderBy'], toArray(orderBy));
        });

        const nextState = state
          .set('context', context)
          .set(context, nextFacets as any)
          .set(
            action.context === Context.facets
              ? 'currentFacet'
              : 'currentListing',
            facet
          )
          .set('term', term)
          .setIn(['tabs', tabId, 'currentFacet'], facet)
          .setIn(
            [context, facet, 'pagingInfo', 'pageIndex'],
            Number(pageIndex) - 1 ||
              (state.getIn([context, facet, 'queryParams', 'loadMorePaging'])
                ? state.getIn([context, facet, 'pagingInfo', 'pageIndex'], 0)
                : 0)
          )
          .setIn(['config', 'isLoaded'], true)
          .setIn(['config', 'ssr'], typeof window === 'undefined');

        return resetAllFacets
          ? nextState.set(context, resetFacets(nextState, context) as any)
          : nextState;
      }
      case SET_SEARCH_ENTRIES: {
        const thisContext = action.context || context;
        const currentFacet = state.getIn([thisContext, action.facet]);
        return state.setIn(
          [thisContext, action.facet],
          currentFacet.merge(fromJS(action.nextFacet))
        );
      }
      case SET_SEARCH_FILTERS: {
        // DO SEARCH then SET_SEARCH_FILTERS is for when we cannot use SET_ROUTE_FILTERS
        // for example in a minilist scenario where the route filters
        // are used for the primary page / listing navigation

        // Add filter values in params to the matched filters in state
        // causing unfetched filter items to be generated with isSelected: true
        const filters = generateFiltersState(
          { ...action, isCurrentFacet: true },
          state
        );

        const term = action?.params?.term;
        const useSearchTerm = state.getIn(
          [
            action.context || Context.minilist,
            action.facet,
            'queryParams',
            'useSearchTerm',
          ],
          false
        );

        return state
          .setIn(
            [action.context || Context.minilist, action.facet, 'filters'],
            filters
          )
          .setIn(
            [
              action.context || Context.minilist,
              action.facet,
              'queryParams',
              'excludeIds',
            ],
            fromJS(action.excludeIds)
          )
          .set('term', useSearchTerm ? term : state.get('term'))
          .setIn(['config', 'ssr'], typeof window === 'undefined');
      }
      case UPDATE_PAGE_INDEX: {
        const { pageIndex } = action;
        const internalPaging = state.getIn(
          [context, current, 'queryParams', 'internalPaging'],
          false
        );

        const currentPageIndex = state.getIn(
          [context, current, 'pagingInfo', 'pageIndex'],
          0
        );

        const nextState = state
          .setIn(
            [context, current, 'pagingInfo', 'pageIndex'],
            Number(pageIndex) || 0
          )
          .setIn(
            [context, current, 'pagingInfo', 'prevPageIndex'],
            currentPageIndex
          )
          .setIn([context, current, 'pagingInfo', 'isLoading'], true);

        if (internalPaging) return nextState;

        return nextState.setIn([context, current, 'queryDuration'], 0);
      }
      case UPDATE_SEARCH_TERM: {
        return state
          .set('term', action.term)
          .set(context, resetFacets(state, context) as any);
      }
      case UPDATE_SELECTED_FILTERS: {
        const { filter, key } = action;

        const isSingleSelect = state.getIn(
          [context, current, 'filters', filter, 'isSingleSelect'],
          false
        );
        const isGrouped = state.getIn(
          [context, current, 'filters', filter, 'isGrouped'],
          false
        );

        const currentItems = state.getIn([
          context,
          current,
          'filters',
          filter,
          'items',
        ]) as List<typeof filterItem>;
        return state
          .set(
            context,
            isGrouped
              ? (resetFacets(state, context) as any)
              : (state.get(context) as OrderedMap<string, typeof searchFacet>)
          )
          .setIn(
            [context, current],
            resetFacet(state.getIn([context, current]))
          )
          .setIn(
            [context, current, 'filters', filter, 'items'],
            currentItems.map((item = Map()) => {
              if (item.get('key') === key) {
                return item.set('isSelected', !item.get('isSelected'));
              }
              return isSingleSelect ? item.set('isSelected', false) : item;
            })
          );
      }
      case UPDATE_SORT_ORDER: {
        const { orderBy, facet } = action;
        return state
          .set(context, resetFacets(state, context) as any)
          .setIn(
            [context, facet || current, 'queryParams', 'dynamicOrderBy'],
            orderBy ? fromJS(toArray(orderBy)) : ''
          );
      }
      default:
        return state;
    }
  };
};
