import { List, Map, OrderedMap } from 'immutable';
import { CustomWhereClause } from '../models/Search';
import { Filter, FilterItem, SearchState } from '../models/SearchState';

export const entries = Map({
  isLoading: false,
  isError: false,
  items: List(),
});

export const pagingInfo = Map({
  isLoading: false,
  pageCount: 0,
  pageIndex: 0,
  pageSize: 0,
  pagesLoaded: List(),
  prevPageIndex: 0,
  totalCount: 0,
});

export const searchFacet = OrderedMap({
  title: null,
  featuredEntries: entries,
  featuredResults: List(),
  entries,
  results: List(),
  queryParams: Map(),
  filters: Map(),
  queryDuration: 0,
  pagingInfo,
  projectId: '',
});

export const searchTab = Map({
  currentFacet: undefined,
  facets: OrderedMap(),
  id: 0,
  label: undefined,
  totalCount: '',
});

export const filtering = Map({
  isLoading: false,
  isError: false,
  isGrouped: false,
  title: undefined,
  contentTypeId: undefined,
  customWhere: List(),
  fieldId: undefined,
  items: List<typeof filterItem>(),
} as Omit<Filter, 'customWhere' | 'items'> & { customWhere?: List<CustomWhereClause>; items?: List<typeof filterItem> });

export const filterItem = Map({
  key: '',
  type: undefined,
  title: undefined,
  path: undefined,
  isSelected: false,
} as FilterItem);

const config = Map({
  isLoaded: false,
  isError: false,
});

const searchState: SearchState = {
  currentFacet: '',
  term: '',
  facets: OrderedMap(),
  tabs: List(),
  config,
};

export const initialState = OrderedMap(searchState);
